@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,300;1,100&display=swap");
* {
  background-image: url(./images/bg.jpg);
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}
body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
}
.container {
  width: 100%;
}
.mainDiv {
  width: 100%;
  flex-wrap: wrap;
}
.searchBar {
  height: 8%;
  width: 60%;
  font-size: 20px;
  display: flex;
  border-color: aliceblue;
  box-shadow: none !important;
  border: 1px solid aliceblue !important;
  line-height: 1.1em !important;
  border-radius: 0px !important;
  margin: 0 auto;
  border-radius: 10px;
  color: aliceblue;
  margin-top: 3%;
}
.loadMoreBtn {
  background-color: aliceblue;
  min-width: 35%;
  min-height: 10%;
  position: absolute;
  cursor: pointer;
  bottom: auto;
  margin-right: auto;
  margin-left: auto;
  left: 50%;
  transform: translate(-50%, 0);
  background-image: none;
  font-weight: 800;
}
.loadMoreBtn:hover {
  background-color: rgb(185, 221, 249);
}

.searchBar::placeholder {
  padding-left: 10px;
  color: aliceblue;
}
.videosContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 1px;
  margin-top: 10px;
}
iframe {
  border-radius: 15px;
  margin: 1px;
  margin-left: 18px;
  margin-right: 18px;
}
@media (max-width: 600px) {
  iframe {
    border-radius: 15px;
    align-items: center;
    margin: 1px;
    width: 42%;
    max-height: 10%;
    margin-left: 18px;
    margin-right: 18px;
  }
}
